.memorama {
    text-align: center;
    display: grid;
    grid-template-columns: 2fr 3fr 2fr;
    grid-template-rows: 1fr;
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(4, 150px);
    grid-gap: 20px;
    justify-content: center;
    text-align: center;
  }
  
  .card {
    width: 150px; 
    height: 150px;
    background-color: #ccc;
    cursor: pointer;
    position: relative;
    transform: rotateY(0);
    transform-style: preserve-3d;
    transition: transform 0.5s;
  }
  
  .card.flipped {
    transform: rotateY(180deg);
  }
  
  .front, .back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .front {
    background-color: #f0f0f0;
  }
  
  .back {
    transform: rotateY(180deg);
  }
  
  .back img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .P1 img, .P2 img{
    width: 400px;
    aspect-ratio: 1/1;
  }
  